@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: "Open Sans", serif;
}

.wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(45, 176, 224, 1);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45, 176, 224, 1)), color-stop(100%, rgba(96, 40, 168, 1)));
    background: -webkit-linear-gradient(45deg, rgba(45, 176, 224, 1) 0%, rgba(96, 40, 168, 1) 100%);
    background: linear-gradient(45deg, rgba(45, 176, 224, 1) 0%, rgba(96, 40, 168, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1);
}

.main {
    background: #2a6bb1;
    box-shadow: 0px 2px 80px -20px rgba(0, 0, 0, 0.5);
    margin: 0 auto;
}

.info {
    background: url(/cities/static/media/polygon.cc54c19b.jpg) center center no-repeat;
    background-blend-mode: multiply;
    background-color: #484747;
    border: 2px solid #1f1f1f;
    border-right: 2px solid #131e29;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #000;
}

form {
    padding-top: 6em;
}

.form {
    border-top: 2px solid #113a65;
    border-bottom: 2px solid #113a65;
    border-right: 2px solid #113a65;
    padding: 1em;
    background: rgba(45, 176, 224, 1);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45, 176, 224, 1)), color-stop(100%, rgba(96, 40, 168, 1)));
    background: -webkit-linear-gradient(45deg, rgba(45, 176, 224, 1) 0%, rgba(96, 40, 168, 1) 100%);
    background: linear-gradient(45deg, rgba(45, 176, 224, 1) 0%, rgba(96, 40, 168, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1);
}

input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 2px #113a65;
    width: 50%;
    padding-bottom: 4px;
    color: #fff;
    font-weight: lighter;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
    outline: none;
}

input::-webkit-input-placeholder {
    color: #103a65;
}

input::-moz-placeholder {
    color: #103a65;
}

button {
    border: 2px solid #103a65;
    border-bottom: 4px solid #103a65;
    padding: 5px 15px;
    margin: 0 2px;
    border-radius: 2px;
    cursor: pointer;
    background-color: #235d9c;
    color: #fff;
    outline: none !important;
}

button:hover {
    border-bottom: 2px solid #103a65;
    position: relative;
    top: 2px;
}

button:active {
    border-bottom: 2px solid transparent;
    border-color: transparent;
    position: relative;
    top: 2px;
}

.info h2 {
    font-size: 3em;
    color: #fff;
}

.info p {
    color: #e4e4e4;
    font-weight: bold;
}

.infoWeath {
    font-size: 20px;
    color: #fff;
    margin-top: 2em;
    height: 9em;
}

.error {
    color: #041f3c;
    font-weight: bold;
}


@media (min-width: 768px) and (max-width: 1024px) {
    form {
        padding-top: 5em;
    }

    button {
        padding: 8px 15px;
    }
}

@media (min-width: 575px) and (max-width: 767px) {
    form {
        display: flex;
        flex-direction: column;
        padding-top: 4em;
    }

    input[type="text"] {
        width: 100%;
        margin-right: 0;
    }
}

@media (min-width: 320px) and (max-width: 480px) {
    .wrapper {
        height: auto;
    }
    .form {
        height: 70vh;
    }
    form {
        display: flex;
        flex-direction: column;
        padding-top: 0;
        /*height: 70vh;*/
    }

    input[type="text"] {
        width: 100%;
        margin-right: 0;
        margin-bottom: 20px;
    }

    .info {
        height: 30vh;
    }
    .infoWeath {
        margin-top: 1em;
    }
}
